:root {
  --primary:#2F80ED;
  --white: #ffffff;
  --dark-primary:#21333B;
  --grey:#F2F2F2;
}


body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}
.container {
   width: 80%; 
   margin: auto;
}

.line-image {
  background-image: url('./images/header-line.png');
  width: 100%;
  height: 289px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left;
  position: absolute;
}

.spacer {
  padding-bottom: 5rem;
  padding-top: 5rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dark-mode {
  background: var(--dark-primary);
}

.ant-table-thead > tr > th {
  display: none;
}

.ant-table-tbody > tr.ant-table-row:hover > td {
  background: transparent;
}

.all-pools-heading {
  font-family: 'Inter';
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  position: relative;
  
  text-transform: capitalize;
  
  color: #000000;
  
}

/* all pools page desighn */

.search {
  background: #FFFFFF;
  border: 1px solid #BDBDBD;
