  font-family: 'Poppins';
  font-style: normal;
  font-weight: 300;
  padding: 18px 45px;
  font-size: 14px;
  line-height: 21px;
   color: #828282;
}

.all-pools {
  margin-top: 8rem;
}

.anticon svg {
  color: #828282;
  font-size: 21px;
  margin-top: -56px;
  position: absolute;
  margin-left: 16px;
}

.shape {
  background: var(--primary);
  border-radius: 12px;
  outline: none;
  padding: 12px;
  margin-left: -8px;
  border: 1px solid #2F80ED;
}

.feature-content-div {
  margin-left: -14px;

}

  .feature-content-div > div {
  margin-left: 14px !important;
  margin-top: 14px;
}

.all-pools .feature-content-div > div {
  padding: 2rem 1rem 1rem 1rem;
  margin-bottom: 1rem
}

.danger {
